import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { TweenMax, TimelineMax, Power1 } from 'gsap';
import Home from './components/Home';
import Menu from './components/Menu';
import SideMenu from './components/SideMenu';
import './styles/SideMenu.scss';
import './styles/Menu.scss';
import './styles/Home.scss';
import './styles/Content.scss';
import './styles/normalize.css';


class App extends Component{

  constructor(props){
    super(props);
    this.toggle = this.toggle.bind(this);

    /*Variables for menu*/
    this.tl = null;
    this.menuclose = null;
    // this.cont = null;
    this.wait = false;
  }
  
  componentDidMount() {
    this.wait = false;
    this.cont = document.getElementsByClassName("side-container");
    this.menuclose = document.getElementsByClassName("menu-close");
    this.tl = new TimelineMax({paused: true, ease:Power1.easeInOut});
    this.tl.to(this.menuclose, 0.5, {rotateY: "0", delay: 1})
        .to(this.menuclose, 0.3, {clipPath: 'polygon(20% 0%, 0% 20%, 0% 50%, 0% 80%, 20% 100%, 50% 100%, 80% 100%, 100% 80%, 70% 50%, 100% 20%, 80% 0%, 50% 0%'})
        .to(this.menuclose, 0.3, {clipPath: 'polygon(20% 0%, 0% 20%, 0% 50%, 0% 80%, 20% 100%, 50% 100%, 80% 100%, 100% 80%, 70% 50%, 100% 20%, 80% 0%, 50% 30%'})
        .to(this.menuclose, 0.3, {clipPath: 'polygon(20% 0%, 0% 20%, 30% 50%, 0% 80%, 20% 100%, 50% 100%, 80% 100%, 100% 80%, 70% 50%, 100% 20%, 80% 0%, 50% 30%'})
        .to(this.menuclose, 0.3, {clipPath: 'polygon(20% 0%, 0% 20%, 30% 50%, 0% 80%, 20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%, 100% 20%, 80% 0%, 50% 30%'});
    
  }

  toggle() {
    var chk = document.getElementById("chkMenu");
    var menuContent = document.getElementsByClassName("menu-content");
    var menuUnderline = document.querySelectorAll(".menu-underline");
    
    
    if (chk.checked == false && this.wait == false) {
        this.wait = true;
        TweenMax.to(menuContent, 1, {left: "0"});
        TweenMax.to(menuUnderline[0], 1, {rotateY: "0", delay: 0.9});
        TweenMax.to(menuUnderline[1], 1, {rotateY: "0", delay: 1.4});
        TweenMax.to(menuUnderline[2], 1, {rotateY: "0", delay: 1.9});
        setTimeout(()=>{
          this.wait = false
        }, 2050); 
        this.tl.play(0);
        chk.checked = true;
    }
    else if (chk.checked == true && this.wait == false) {
      this.wait = true;
        TweenMax.to(menuContent, 1, {left: "-200", delay: 2.5});
        TweenMax.to(menuUnderline[2], 0.8, {rotateY: "90", delay: 0.9});
        TweenMax.to(menuUnderline[1], 0.8, {rotateY: "90", delay: 1.4});
        TweenMax.to(menuUnderline[0], 0.8, {rotateY: "90", delay: 1.9});
        setTimeout(()=>{
          this.wait = false
        }, 2050); 
        this.tl.reverse();
        chk.checked = false;
    }
    
  }

  lineHover(nOne, nTwo) {
    var je = document.querySelectorAll(".menu-underline");
    TweenMax.to(je[nOne], 1, {rotateY: "90"});
    TweenMax.to(je[nTwo], 1, {rotateY: "90"});
  }

  lineExit(nOne, nTwo) {
    var it = document.querySelectorAll(".menu-underline");
    TweenMax.to(it[nOne], 1, {rotateY: "0"});
    TweenMax.to(it[nTwo], 1, {rotateY: "0"});
  }

  contact() {
    var co = document.getElementsByClassName("contact");
    TweenMax.to(co, 1, {top: "90%"});
    TweenMax.to(co, 1, {top: "101%", delay: 5});
  }
  // open() {
  //   var maindiv = document.getElementsByClassName("main");
  //   var theX = document.getElementsByClassName("close2");

  //   TweenMax.to(this.cont, 1, {right: "-200"});
  //   TweenMax.to(maindiv, 1, {marginRight: "250px", delay: 1});
  //   TweenMax.to(theX, 1.5, {opacity: 1, delay: 1});
  // }

  // close() {
  //   var maindiv = document.getElementsByClassName("main");
  //   var theX = document.getElementsByClassName("close2");

  //   TweenMax.to(this.cont, 1, {marginRight: "-250px"});
  //   TweenMax.to(maindiv, 1, {marginRight: "-250px", delay: 1});
  //   TweenMax.to(theX, 1.5, {opacity: 0});
  // }

  render() {
    // const { toggle} = this.props;
    return(
      <Router>

          <div className="global">
            

            <div className="menu-content">
                <div onClick={()=>{this.toggle()}} className="menu-close" id="yes">

                  <img src="images/backgroundFb.jpg" alt="bck"></img>
                </div>
                <h3 onMouseEnter={()=>{this.lineHover(1,2)}} onMouseLeave={()=>{this.lineExit(1,2)}}><a href="https://www.linkedin.com/in/allen-jang/" target="_blank">LinkedIn</a></h3>
                <div className="menu-underline"></div>
                <h3 onMouseEnter={()=>{this.lineHover(0,2)}} onMouseLeave={()=>{this.lineExit(0,2)}}><a href="https://www.youtube.com/channel/UCXqeyzDfpRX8mL6n8CHJz5A/videos?view_as=subscriber" target="_blank">Youtube</a></h3>
                <div className="menu-underline"></div>
                <h3 onMouseEnter={()=>{this.lineHover(0,1)}} onMouseLeave={()=>{this.lineExit(0,1)}} onClick={()=>{this.contact()}}><a>Contact</a></h3>
                <div className="menu-underline"></div>
            </div>
            
            {/* <div onClick={()=>{this.open()}} class="open">
                Open
            </div>   */}
            
            <div className="sideMenu">
            
              <Route
                path="/"
                render = { ()=>
                  <SideMenu/>
                }
              />
            </div>
            
            <div className="main">
                {/* <div onClick={()=>{this.close()}} class="close2">
                  X
                </div> */}

              <Route
                path="/"
                render = { ()=>
                  <Menu toggle={() => this.toggle()}/>
                }
              />
              
              <Route
                path="/"
                render = { ()=>
                  <Home/>
                }
              />

            </div>
            <div className="contact">
                <p>nellagnaj@gmail.com</p>
            </div>
          </div>
      </Router>
    )
  }
}

export default App;
