import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';

import { TweenLite, TimelineMax  } from 'gsap/all';
import 'regenerator-runtime/runtime';

import { TweenMax } from 'gsap/gsap-core';

let realInfo = "You are one. But one too many. Thats why..";
let realTitre = "Web : Yare yare";
let realImage = "";
let realId = 0;
let realLink = "";
export const addContent = function(sTitre, sDesc, sImage, id, contentToggle, link){
    realTitre = sTitre;
    realInfo = sDesc;
    realImage = sImage;
    realId = id;
    realLink = link;
    let ie = new Home().set(contentToggle);
} 


class Home extends Component {
    constructor(props) {
        super();
        this.info = null;
        this.infoContainer = React.createRef();
        this.imgContainer = null;
        this.imgInitialX = null;
        this.imgInitialY = null;
        this.tl = null;
        this.tlReversed = null;

        //--For the mouse effect--
        this.element = React.createRef();
        this.width = null;
        this.height = null;
        this.left = null;
        this.top = null;
        //------------------------

        this.state = {
            titre: "",
            text: "",
            image: "",
            link: "",
        }
    }
    
    componentDidMount() {
        TweenLite.set('.introline, .secondline, .middleline', {y:'-101%'});
        var lines = new TimelineMax({repeat:0, yoyo:true})
        .to('.introline',1,{y:'0%', delay: 1})
        .to('.middleline',1,{y:'0%'})
        .to('.secondline',1,{y:'0%'});
        
        this.setState({
            titre: realTitre,
            text: realInfo,
            image: realImage,
            link: realLink
        })
        setInterval(()=> {
            this.check();
        }, 100);
        
        this.imgContainer = document.getElementById("im");
        // this.imgContainer = document.getElementById(realId);
        // console.log(this.imgContainer.getBoundingClientRect().x);
    }

    set(toggle) {
        TweenLite.set(".infoTitre, .infoText", {perspective: '100px' ,rotateX: '90deg'});
        var perps = new TimelineMax({repeat:0, yoyo: true})
        .to('.infoText, .infoTitre', 1.5, {perspective: '0' ,rotateX: '0deg'})
        // .to('.infoText', 1, {perspective: '0' ,rotateX: '0deg'});
        var content = document.getElementsByClassName("content-container");
        // console.log(content);

        // this.imgContainer = document.getElementById(realId);   
        // this.imgInitialX = this.imgContainer.getBoundingClientRect().x;
        // this.imgInitialY = this.imgContainer.getBoundingClientRect().top;
        // console.log(this.imgContainer.id, this.imgInitialY);
        
        // this.tl = new TimelineMax({repeat: 0, yoyo: true}).to(this.imgContainer, 1.5, {position: "absolute", left: "150%"})
        // .to(this.imgContainer, .1, {width: 250, height: 200, top: "38%"})
        // .to(this.imgContainer, 1, {position: "fixed", left: "20%", top: "38%"});
        
        if (toggle == false) {
            var open = new TimelineMax({repeat:0, yoyo: true}).to('.content-container', 1, {opacity: 1});
            var open2 = new TimelineMax({repeat:0, yoyo: true}).to('.coverUp', 0.5, {display: "block", zIndex: 18, opacity: 0.5}).to('.coverUp', 0.5, {opacity: 1});
            var open3 = new TimelineMax({repeat:0, yoyo: true}).to('.sideMenu2', 0.5, {display: "block", opacity: 0.5}).to('.sideMenu2', 0.5, {opacity: 1});
            // this.tl.play();
        }
        else if (toggle == true) {
            var close = new TimelineMax({repeat:0, yoyo: true}).to('.content-container', 1, {opacity: 0});
            // this.tl.reversed();
        }
        
    }

    check() {
        
        this.setState({
            titre: realTitre,
            text: realInfo,
            image: realImage,
            link: realLink
        })
    }
    
    handleMouseEnter(e) {
        this.updateElementPosition(e);
        // this.imgContainer = document.getElementById(realId); 
        TweenMax.to(this.imgContainer.current, 1, {rotationX: 0, rotationY: 0});
        // console.log('onMouseEnter', e.clientX, e.clientY)
        // console.log(this.element);
    }

    handleMouseMove(e) {
        // console.log('onMouseMove', e.nativeEvent.clientX, e.nativeEvent.clientY)
        this.updateElementPosition(e);
    }

    handleMouseLeave(e) {
        // this.updateElementPosition();
        // console.log(this.imgContainer);
        TweenMax.to(this.imgContainer.current, 1, {rotationX: 0, rotationY: 0});
    }

    updateElementPosition(e) {
        const rect = this.element.current.getBoundingClientRect();
        this.width = this.element.current.offsetWidth;
        this.height = this.element.current.offsetHeight;
        this.left = rect.left;
        this.top = rect.top;
        
        if (e.clientX > this.left && e.clientX < this.left + this.width
            && e.clientY > this.top && e.clientY < this.top + this.height) {
                
                //Left side
                if (e.clientX > this.left && e.clientX < this.left + (this.width/2)) {
                    // console.log("left");
                    let valueLeft = (this.width/2 - (e.clientX - this.left))/10;
                    TweenMax.to(this.imgContainer, 1, {translateY: -0.65 * valueLeft});
                }
                //Right side
                if (e.clientX > this.left + (this.width/2) && e.clientX < this.left) {
                    // console.log("right");
                    let valueRight = ((e.clientX - this.left) - (this.width/2))/10;
                    TweenMax.to(this.imgContainer, 1, {translateY: 0.65 * valueRight});
                }
                //Top side
                if (e.clientY > this.top && e.clientY < this.top + (this.height/2)) {
                    // console.log("Top");
                    let valueTop = (this.height/2 - (e.clientY - this.top))/10;
                    TweenMax.to(this.imgContainer, 1, {translateX: 0.65 * valueTop});
                }
                //Bottom side
                if (e.clientY > this.top + (this.height/2) && e.clientY < this.top +this.height) {
                    // console.log("Bottom");
                    let valueBottom = ((e.clientY - this.top) - (this.height/2))/10;
                    TweenMax.to(this.imgContainer, 1, {translateX: -0.65 * valueBottom});
                }

        }
        
    }


    render() {
        return (
            <div className="home">
                
                <div className="sideMenu2"></div>
                <div className="coverUp"></div>

                <div className="border-wrap">
                    <div className="wrap4">
                        <h1 className="prenom">Allen</h1>
                    </div>
                    <div className="wrap5">
                        <h1 className="nom">Jang</h1>   
                    </div>
                </div>

                <div className="wrapper">
                    <div className="wrap1">
                        <p className="introline">2019</p>
                    </div>
                    <div className="wrap3">
                        <div className="middleline"></div>
                    </div>
                    <div className="wrap2">
                        <p className="secondline">2020</p>
                    </div>
                </div>

                <div ref={this.element} className="content-container" 
                onMouseEnter={(e)=>{this.handleMouseEnter(e)}}
                onMouseMove={(e)=>{this.handleMouseMove(e)}}
                onMouseLeave={(e)=>{this.handleMouseLeave(e)}}>

                    <div /*ref={this.imgContainer}*/ id="im" className="contentimage">
                        <img src={this.state.image} alt="contentImage"></img>
                    </div>
                    
                    {/* <div className="content-image"></div> */}
                    <div ref={this.infoContainer} className="content-info" id="galaxy">
                            <h2 className="infoTitre"> 
                                {this.state.titre}
                            </h2>
                            <span className="underline2"></span>
                            <p className="infoText">
                                {this.state.text}
                            </p>
                            <br></br>
                            <a href={this.state.link} target="_blank">Show</a>
                    </div>
                    
                </div>

                {/* <input type="checkbox" name="chkContent" id="chkContent"></input> */}
            </div>
        );
    }
}
export default withRouter(Home);