    insertContent(titre, desc, image, id, link) {
        // var chkCont = document.getElementById("chkContent");
        //Open
        if (this.contentToggle == true) {
            this.contentToggle = false;
            // var open = new TimelineMax({repeat:0, yoyo: true}).to('.close-button', 1, {opacity: 1});
            var open2 = new TimelineMax({repeat:0, yoyo: true}).to('.close-button', 0.5, {display: "block"}).to('.close-button', 0.5, {opacity: 1});
        }
        //Close
        else {
            this.contentToggle = true;
        }
        addContent(titre, desc, image, id, this.contentToggle, link);
        // chkCont.checked = true;
        // var content = document.getElementsByClassName("content-container");
        
        
    }

    onHover(id) {
        var img = document.getElementsByClassName("content-image");
        var title = document.getElementsByClassName("cont-title");
        TweenMax.to(img[id], 1, {left: "0%"});
        TweenMax.to(title[id], 1, {left: "-125%"});
    }
    onExit(id) {
        var img = document.getElementsByClassName("content-image");
        var title = document.getElementsByClassName("cont-title");
        TweenMax.to(img[id], 1, {left: "100%"});
        TweenMax.to(title[id], 1, {left: "0%"});
    }

    theClose() {
        if (this.contentToggle != true) {
            this.contentToggle = true;
            var close = new TimelineMax({repeat:0, yoyo: true}).to('.content-container', 1, {opacity: 0});
            var close2 = new TimelineMax({repeat:0, yoyo: true}).to('.close-button', 0.5, {opacity: 0}).to('.close-button', 0.5, {display: "none"});
            var close3 = new TimelineMax({repeat:0, yoyo: true}).to('.sideMenu2', 0.5, {opacity: 0}).to('.sideMenu2', 0.5, {display: "none"});
            var close4 = new TimelineMax({repeat:0, yoyo: true}).to('.coverUp', 0.5, {opacity: 0}).to('.coverUp', 0.5, {display: "none"});
        }
    }

    insertSides() {
        return this.state.info.map((e)=>{
            return <div className="sideChild" onMouseEnter={()=>{this.onHover(e.id)}} key={e.id} onMouseLeave={()=>{this.onExit(e.id)}} onClick={()=>{this.insertContent(e.sTitre, e.sDesc, e.sImgUrl, e.id, e.link)}}>
                <h2 className="cont-title">{e.sTitre}</h2>
                {/* <div class="content-imageHider"></div> */}
                <div ref={this.imgContainer} className="content-image" id={e.id}>
                        <img src={e.sImgUrl} alt="contentImage"></img>
                </div>
            </div>

        })

    }

